    'use client';
    import 'swiper/css';
    import 'swiper/css/navigation';
    import 'swiper/css/pagination';

    import { Swiper, SwiperSlide } from 'swiper/react';
    import { Navigation, Pagination } from 'swiper/modules';

    interface Screenshot {
    image: string;
    }

    type GameImageProps = {
    background_image: string;
    gameName: string;
    screenshots?: Screenshot[];
    };

    export function GameImage({ background_image, gameName, screenshots }: GameImageProps) {
    return (
        <div className="relative rounded-xl shadow-md overflow-hidden group">
        <img
            src={background_image}
            alt={gameName}
            className="w-full h-auto object-cover"
        />

        {screenshots && screenshots.length > 0 && (
            <div className="absolute inset-0 z-10 bg-black bg-opacity-80 opacity-100 flex items-center justify-center">
            <div className="w-full h-full">
                <Swiper
                modules={[Navigation, Pagination]}
                spaceBetween={10}
                slidesPerView={1}
                navigation
                pagination={{ clickable: true }}
                className="h-full min-h-[300px]"
                >
                {screenshots.map((screenshot, index) => (
                    <SwiperSlide key={index} className="flex items-center justify-center">
                    <img
                        src={screenshot.image}
                        alt={`${gameName} screenshot ${index + 1}`}
                        className="max-w-full max-h-full object-contain rounded"
                    />
                    </SwiperSlide>
                ))}
                </Swiper>
            </div>
            </div>
        )}
        </div>
    );
    }
